import React from 'react';
import { Row, Col } from 'antd';
import { UpcomingLessons } from './UpcomingLessons';
import { TasksToDo } from './TasksToDo';
import { Leaderboard } from './Leaderboard';
import { Courses } from './Courses';
import { Homework } from './Homework';


export const Main = () => {
    return (
        <div >

            <Row justify="space-around" style={{ background: 'white' }} >
                <Col style={{border:'2px solid black', padding: '20px' }}><UpcomingLessons></UpcomingLessons></Col>
                <Col style={{border:'2px solid black', padding: '20px' }}><TasksToDo></TasksToDo></Col>
                <Col style={{border:'2px solid black', padding: '20px' }}><Leaderboard></Leaderboard></Col>
            </Row>
            <br/>
            <Row justify="space-around" style={{ background: 'white' }}>
                <Col span={13} style={{padding: '20px' }}><Courses></Courses></Col>
                <Col span={4} style={{border:'2px solid black', padding: '20px',margin:'20px' }}><Homework></Homework></Col>
            </Row>

        </div>
    )
}
