import React from 'react';
import { Main } from './main';
import { Layout, Menu } from 'antd';
import { UserOutlined } from '@ant-design/icons';
const { Content, Sider } = Layout;



export const Sidebar = () => {
  return (
    <div>
      <Layout
        style={{
          margin: '20px',
          height: '100vh',
          display: 'flex',
          background: 'white',
          border: '2px solid black',
          padding:'20px'
        }}>
        <Sider
          style={{
            background: 'white'
          }}
        >
          <Menu mode="inline" defaultSelectedKeys={['4']}>
            <Menu.Item key="1" icon={<UserOutlined />}>
            </Menu.Item>
            <Menu.Item key="2" >
              HOME
        </Menu.Item>
            <Menu.Item key="3" >
              DASHBOARD
        </Menu.Item>
            <Menu.Item key="4" >
              MY COURSES
        </Menu.Item>
            <Menu.Item key="5" >
              ACHIEVEMENTS
        </Menu.Item>
            <Menu.Item key="6" >
              FORUM
        </Menu.Item>
          </Menu>

        </Sider>
        <Content

        ><Main></Main></Content>
      </Layout>
      <br />
    </div>
  )
}
